.footer {
    background-color: rgb(31, 31, 31);
    margin: 0 0;
    color: white;
    min-height: 340px;
    height: max-content;
    box-shadow: 0 5px 5px -5px black;
}

.footer .footer-main {
    background-color: none;
    padding-left: 3%;
    padding-right: 3%;
    padding-top: 30px;
    width: 90%;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    min-height: 200px;
    height: max-content;
    display: flex;
}
.footer-logo {
    max-width: 70px;
    max-height: 70px;
    width: auto;
    height: auto;
}

.footer .footer-main:after {
    content: "";
    background: white;
    position: absolute;
    bottom: 0;
    left: 5%;
    height: 1px;
    width: 90%;
}

.footer .footer-sub {
    background-color: none;
    padding: 30px;
    width: 90%;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    height: max-content;
    display: block;
    margin-bottom: 40px;
}

.footer-sub__copyright {
    display: block;
    position: relative;
    float: right;
    font-size: 11px;
    color: white;
    padding: 7px;
}
.footer-sub .footer-sub__links {
    display: block;
    position: relative;
    float: left;
}

.footer-sub__link-block {
    width: 40px;
    height: 40px;
    display: inline-flex;
    background-color: goldenrod;
    border-radius: 100vw;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-left: 10px;
    margin-right: 10px;
}

.footer-sub__link-block .footer-sub__link-block__icon {
    width: 60%;
    height: 60%;
}

#footer-twitter-icon {
    margin-left: 1.6px;
    margin-top: 2px;
}

@media screen and (max-width: 640px) {
    .footer-sub__copyright {
        margin-top: 10px;
    }
}

.footer-main__column {
    display: flex;
    flex-direction: column;
    width: 33%;
}

.footer-column-title {
    color: goldenrod;
}

.footer-column-link {
    color: white;
    font-size: 11px;
    text-decoration: none;
    display: block;
}
.footer-column-link:hover {
    color: white;
    text-decoration: underline;
}

