/** Footer for mobile */
.footer-mobile-links {
    border-top: 1px gray solid;
    border-bottom: 1px gray solid;
    width: 100%;
    text-transform: uppercase;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    height: 50px;
    cursor: pointer;
    transition: 0.2s;
}

.footer-mobile-links:hover {
    background-color: rgba(255, 255, 255, 0.26);
    transition: 0.2s;
}

.footer-mobile {
    min-height: 460px;
}

.footer-mobile .footer-mobile-header {
    width: 100%;
    height: 140px;
    flex-wrap: wrap;
    border: none !important;
    text-transform: none;
}

.footer-mobile .footer-mobile-header:hover {
    background: transparent;
}

.footer-mobile .footer-mobile-header img {
    width: 60px;
    height: 60px;
}

.footer-mobile .footer-mobile-header h6 {
    text-align: center;
    width: 100%;
}
.footer-sub__link-block.mobile {
    margin: 10px !important;
}

.footer-link_block-container.mobile {
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer-sub__copyright.mobile {
    margin-top: 0;
}
